@import "../common/styles/sass/variables.scss";
@import "../common/styles/sass/mixins";

.footerBlock {
    min-height: 25vh;
    background-color: $bgColor;

    .footerContainer {
        align-items: center;
        flex-direction: column;
        @include container();

        .footerSquares {
            max-width: 330px;
            width: 100%;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            box-sizing: border-box;

            .square {
                width: 50px;
                height: 50px;
                background-color: $bgColor;
                background-size: cover;
                line-height: 62px;
                margin: 25px 15px;
                box-shadow: 10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
                border-radius: 6px;
                transition:  0.4s;
                display: inline-block;
                text-align: center;
                font-size: 20px;
                color: #c4cfde;

                @media screen and (max-width: 767px) {
                    width: 40px;
                    height: 40px;
                }

                &:hover {
                    background: $redColor;
                    background-size: cover;
                    transform: translateY(-5px);

                }
            }
        }

        h2 {
            color: $textColor;
            letter-spacing: 1px;
            font-size: 15px;
            font-weight: 400;

        }

        h3 {
            color: $textColor;
            letter-spacing: 1px;
            text-transform: uppercase;
            font-size: 18px;
            font-weight: 500;
            margin-top: 20px;
        }

    }
}







